import { Component, OnInit } from '@angular/core';
import {
  ICartela,
  INumerosCartela,
  IRequestJogos,
} from 'app/shared/models/interface.model';
import { JogosService } from 'app/shared/services/jogos.service';
import { LotofacilService } from '../../api/lotofacil.service';

@Component({
  selector: 'app-base-ultimo',
  templateUrl: './base-ultimo.component.html',
  styleUrls: ['./base-ultimo.component.scss'],
})
export class BaseUltimoComponent implements OnInit {
  numerosCartela: INumerosCartela[] = [];

  cartelas: ICartela[] = [];

  mostrarNovo = true;

  constructor(
    private service: LotofacilService,
    private jogosService: JogosService
  ) {}

  ngOnInit(): void {
    this.gerarJogo();
  }

  gerarJogo(): void {
    this.cartelas = [];
    this.mostrarNovo = false;
    this.service.baseNoUltimo().subscribe({
      next: (response) => {
        this.mostrarNovo = true;
        response.jogos.forEach((jogo) => {
          this.gerarNumerosCartela(jogo);
        });
      },
      error: (erro) => {
        this.mostrarNovo = true;
      },
    });
  }

  gerarNumerosCartela(jogo: number[]): void {
    this.numerosCartela = [];
    for (let i = 1; i < 26; i++) {
      let numeroCartela: INumerosCartela = {
        numero: i,
        selecionado: false,
      };
      numeroCartela.selecionado = jogo.includes(i);
      this.numerosCartela.push(numeroCartela);
    }

    this.cartelas.push({
      cartela: this.numerosCartela,
      selecionados: jogo,
    });
  }

  baixarJogos(): void {
    let requestJogos: IRequestJogos = {
      cartela: [],
    };

    this.cartelas.forEach((car) => {
      requestJogos.cartela.push(car.selecionados);
    });

    this.jogosService.getJogosTXT(requestJogos).subscribe({
      next: (response) =>
        this.jogosService.download('lotofacil-base-ultimo.txt', response),
      error: (erro) => console.log(erro),
    });
  }
}
