import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment as env } from 'environments/environment';
import { Observable } from 'rxjs';
import {
  IFiltroFechamento,
  IJogo,
  IResponseBaseUltimo,
  IResponseFechamento,
} from '../models/lotofacil.model';

@Injectable({
  providedIn: 'root',
})
export class LotofacilService {
  readonly apipath = '/lotofacil';

  constructor(private http: HttpClient) {}

  naoSorteado(): Observable<IJogo> {
    return this.http.get<IJogo>(`${env.apiJogos}${this.apipath}/nao-sorteado`);
  }

  baseNoUltimo(): Observable<IResponseBaseUltimo> {
    return this.http.get<IResponseBaseUltimo>(
      `${env.apiJogos}${this.apipath}/base-ultimo`
    );
  }

  aleatorio(): Observable<IJogo> {
    return this.http.get<IJogo>(`${env.apiJogos}${this.apipath}/base-ultimo`);
  }

  fechamento(filtro: IFiltroFechamento): Observable<IResponseFechamento> {
    const parametros = new HttpParams()
      .set('total_numeros_fechamento', filtro.total_numeros_fechamento)
      .set('para_acertar', filtro.para_acertar)
      .set('numeros_fechamento', filtro.numeros_fechamento)
      .set('geracao_numeros', filtro.geracao_numeros);

    return this.http.get<IResponseFechamento>(
      `${env.apiJogos}${this.apipath}/fechamento`,
      {
        params: parametros,
      }
    );
  }
}
