import { Component, OnInit } from '@angular/core';
import { ICartela, INumerosCartela } from 'app/shared/models/interface.model';
import { LotofacilService } from '../../api/lotofacil.service';

@Component({
  selector: 'app-nao-sorteado',
  templateUrl: './nao-sorteado.component.html',
  styleUrls: ['./nao-sorteado.component.scss'],
})
export class NaoSorteadoComponent implements OnInit {
  numerosCartela: INumerosCartela[] = [];
  numerosSelecionados: number[] = [];

  cartelas: ICartela[] = [];

  mostrarNovo = true;

  constructor(private service: LotofacilService) {}

  ngOnInit(): void {
    this.gerarJogo();
  }

  gerarJogo(): void {
    this.mostrarNovo = false;
    this.service.naoSorteado().subscribe({
      next: (response) => {
        this.mostrarNovo = true;
        this.numerosSelecionados = response.Jogo;
        this.gerarNumerosCartela();
      },
      error: (erro) => {
        this.mostrarNovo = true;
      },
    });
  }

  gerarNumerosCartela(): void {
    this.numerosCartela = [];
    for (let i = 1; i < 26; i++) {
      let numeroCartela: INumerosCartela = {
        numero: i,
        selecionado: false,
      };
      numeroCartela.selecionado = this.numerosSelecionados.includes(i);
      this.numerosCartela.push(numeroCartela);
    }

    this.cartelas.push({
      cartela: this.numerosCartela,
      selecionados: this.numerosSelecionados,
    });
  }

  testarJogo(cartela: ICartela) {
    alert(`NÃ£o implementado ${JSON.stringify(cartela.selecionados)}`);
  }
}
